define(['jquery'],
    function($) {

    

 
        return {
            usernav_slider : function(subtab){
              //console.log(subtab);
                var selector = '#'+subtab+'.divslider .userdashboard_content';

                //$(".course-next").click(function () {               
                var div = $("."+subtab)[0];
                var html = $(div).html();
                console.log(div);
                $(div).remove();
                // console.log(selector);
                $(selector).append("<div class=\"col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12 courses_inprogress_slide ctype_elearning "+subtab+"\">" + html + "</div>");
                // });

            },
            usernavrev_slider:function(subtab,index){
                //$(".course-prev").click(function () {               
                var selector = '#'+subtab+'.divslider .userdashboard_content';
                var div = $("."+subtab)[index];
                var html = $(div).html();
                console.log(div);
                $(div).remove();
                // console.log(selector);
                $(selector).prepend("<div class=\"col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12 courses_inprogress_slide ctype_elearning "+subtab+"\">" + html + "</div>");
                // });
            }

        }
   
});
